/* --------- Dropdown ---------- */
.drop-nav{
  position: relative  !important;
  padding: 10px 0  !important;
}
.drop-nav a{
  text-transform: capitalize  !important;
  overflow: hidden  !important;
}
.drop-nav .dropdown{
  position: absolute  !important;
  background: white  !important;
  width: 100%  !important;
  width: calc(100% + 30px)  !important;
  top: 50px  !important;
  display: none  !important;
  
}
.drop-nav .dropdown li{
  display: block  !important;
}
.drop-nav:hover .dropdown{
  display: block  !important;
}


  
@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
